function sliderMain() {
    new Swiper(".main .mySwiper",{
        slidesPerView: 1,
        spaceBetween: 15,
        loop: !0,
        effect: "fade",
        navigation: {
            nextEl: ".main .swiper-button-next",
            prevEl: ".main .swiper-button-prev"
        },
        pagination: {
            el: ".main .swiper-pagination",
            type: "fraction",
        },
        breakpoints: {
            320: {
                pagination: {
                    type: "bullets",
                },
            },
            577: {
                pagination: {
                    type: "fraction",
                },
            }
        }
    })
}

