$(document).ready(function () {
    sliderMain();
    sliderIndustries();
    sliderNews();
    sliderTabsHead();
    sliderBreadcrumbs();
    sliderCatalog2();
    sliderNewsDesc();
    sliderSearchHead();
    sliderAlphabetTop();
    sliderAboutMain();
    sliderStaff();

    // Menu
    $(".menu-item").hover(onIn, onOut);
    $(".menu-item .menu-item__show").click(function() {
        if ($(this).parent(".menu-item").hasClass("menu-item--active")) {
            $(this).parent(".menu-item").removeClass("menu-item--active")
        } else {
            $(this).parent(".menu-item").addClass("menu-item--active")
        }
    })

    $('.back').click(function () {
        $(this).closest('.menu-item').removeClass('menu-item--active')
    })

    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-bottom').toggleClass('header-bottom--active')
        $('body').toggleClass('fixed-body')
    })

    // Tabs
    $(".tab").click((function() {
        let e = $(this).attr("data-tab-path");
        $(this).siblings(".tab").removeClass("tab--active"),
           $(this).addClass("tab--active"),
           $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active"),
           $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${e}"]`).addClass("tab__content--active")
        }
    ))

    // Select
    $(".custom_select select").niceSelect();

    $('.page-top__select-show').click(function () {
        $(this).parents('.page-top__select').toggleClass('page-top__select--active')
        if (window.matchMedia("(min-width: 1024px)").matches) {
            $(this).parents('.page-top__select').siblings('.page-top__select').removeClass('page-top__select--active')
        }
    })

    if (window.matchMedia("(min-width: 1024px)").matches) {
        let select = $('.page-top__select')
        $(document).click(function (e) {
            if ( !select.is(e.target) && select.has(e.target).length === 0) {
                select.removeClass('page-top__select--active')
            }
        })
    }

    $('.option').click(function () {
        $(this).parents('.form-group').find('.current').addClass('active')
    })

    // Filter
    $('.page-top__filter-btn').click(function () {
        $('.page-top__selections-wrap').addClass('filter--active')
        $('.filter-bg').addClass('filter-bg--active')
        $('body').addClass('fixed-body')
    })
    $('.page-top__selections-btn').click(function () {
        $('.page-top__selections-wrap').removeClass('filter--active')
        $('.filter-bg').removeClass('filter-bg--active')
        $('body').removeClass('fixed-body')
    })

    // Calc
    $(".cartcalc .ccalc-minus").click((function() {
        let e = $(this).closest(".cartcalc").find("input").val();
        if (e > 1) {
            let t = +e - 1;
            $(this).closest(".cartcalc").find("input").val(t)
        } else
            $(this).closest(".cartcalc").find("input").val(e)
        }
    )),
        $(".cartcalc .ccalc-plus").click((function() {
            let e = +$(this).closest(".cartcalc").find("input").val() + 1;
            $(this).closest(".cartcalc").find("input").val(e)
        }
    ));

    // AddСart
    $('.catalog-card__btn').click(function () {
        $(this).toggleClass('btn-yellow')
        $(this).siblings('.cartcalc').toggleClass('cartcalc--active')
    })


    $(".item-show").click((function() {
        $(this).toggleClass("item-show--active"),
        $(this).siblings(".item-hidden").slideToggle()
    }))

    // Input-file
    const dt = new DataTransfer();

    $("#attachment").on('change', function(e){
        for(var i = 0; i < this.files.length; i++){
            let fileBloc = $('<span/>', {class: 'file-block'}),
                fileName = $('<span/>', {class: 'name', text: this.files.item(i).name});
            fileBloc.append('<span class="file-delete"><span>+</span></span>')
                .append(fileName);
            $("#filesList > #files-names").append(fileBloc);
        };
        for (let file of this.files) {
            dt.items.add(file);
        }
        this.files = dt.files;

        $('span.file-delete').click(function(){
            let name = $(this).next('span.name').text();
            $(this).parent().remove();
            for(let i = 0; i < dt.items.length; i++){
                if(name === dt.items[i].getAsFile().name){
                    dt.items.remove(i);
                    continue;
                }
            }
            document.getElementById('attachment').files = dt.files;
        });
    });

    // Input-password
    $('.password-btn').click(function () {
        if ($(this).siblings('.password').attr('type') == 'password'){
            $(this).siblings('.password').attr('type', 'text');
        } else {
            $(this).siblings('.password').attr('type', 'password');
        }
        return false;
    })

    // Basket-check
    $('.select-all__check').click(function () {
        $(this).toggleClass('select-all__check--active')
        $('.basket-main__cards').find('.basket-card__check').toggleClass('active-check')
        if ($('.basket-card__check').hasClass('active-check')) {
            $('.basket-card__check').addClass('active-check')
        }
    })

    $('.basket-card__check').click(function () {
        $(this).toggleClass('active-check')
        if ($('.basket-card__check').not('active-check')) {
            $('.select-all__check').removeClass('select-all__check--active')
        }
    })

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})

function onIn() {
    if (window.innerWidth > 1023) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("menu-item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 1023) {
        $(this).removeClass("menu-item--active")
    }
}

